import Vue from "vue";
import App from "./App.vue";
import router from "./starterRouter";
import Argon from "./plugins/argon-kit";
import BootstrapVue from 'bootstrap-vue'


Vue.config.productionTip = false;
Vue.use(Argon);
Vue.use(BootstrapVue);
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router,
  render: h => h(App)
}).$mount("#app");
